<template>
  <main class="main">
    <div class="container">
      <h1 class="main-title">Todos</h1>
      <div class="main__box">
        <div class="main__box-item" v-for="todo in allTodos" :key="todo.id">
          <h3><span>Пользователь №</span>{{ todo.userId }}</h3>
          <p><span>ID todo:</span> {{ todo.id }}</p>
          <p><span>Название todo:</span> {{ todo.title }}</p>
          <p><span>Статус:</span> {{ todo.completed }}</p>
          
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  methods: {
    ...mapActions(["getTodos"]),
  },
  computed: {
    ...mapState(["allTodos"]),
  },
  created() {
    this.getTodos();
  },
 
};
</script>

